import Img from 'gatsby-image'
import React from 'react'
import pageQuery from '../pages/faculty'
export const FacultyCard = props => (
  <div id="employee_card" class="col-4 employee_card">
    <Img
      fluid={document.node.frontmatter.profile.childImageSharp.fluid}
      alt={document.node.frontmatter.title}
      style={{
        margin: '1rem',
        overflow: 'hidden',
        position: 'relative',
        objectFit: 'cover',
        marginLeft: 'auto',
        marginRight: 'auto',
        width: '200px',
        height: '200px',
        borderRadius: '20px',
      }}
      imgStyle={{ objectPosition: '50% 15%' }}
    />
    <h3 class="faculty-name">{document.node.frontmatter.name}</h3>
    <h4>{document.node.frontmatter.title}</h4>
    <div
      className="blog-post-content"
      dangerouslySetInnerHTML={{ __html: document.node.html }}
    />
  </div>
)

export const FacultyCardBack = props => (
  <div id="employee_card_back" class="col-4 employee_card">
    <div
      className="blog-post-content"
      dangerouslySetInnerHTML={{ __html: document.node.html }}
    />
  </div>
)
